<template>
	<div class="pt-2 relative text-gray-600">
		<input
			class="search-input relative border-2 border-gray-300 bg-white h-10 px-5 pr-16 rounded-lg text-sm focus:outline-none"
			type="search"
			v-model="searchTerm"
			name="search"
			v-debounce:300="search"
			placeholder="Search"
			autocomplete="off"
		/>
		<div
			class="bg-white absolute search-results p-2 shadow-lg mt-2 rounded-md"
			v-if="showResult"
		>
			<div
				class="prose prose-xs hover:bg-teal-100 p-2 rounded"
				v-for="result in searchResults"
				:key="result.name"
			>
				<h4>{{ result.doc.meta_title }}</h4>
				<p class=" text-xs" v-html="result.content_highlights"></p>
			</div>
		</div>
		<span class="absolute right-0 top-0 mt-5 mr-4">
			<svg
				class="text-gray-600 h-4 w-4 fill-current"
				xmlns="http://www.w3.org/2000/svg"
				xmlns:xlink="http://www.w3.org/1999/xlink"
				version="1.1"
				id="Capa_1"
				x="0px"
				y="0px"
				viewBox="0 0 56.966 56.966"
				style="enable-background:new 0 0 56.966 56.966;"
				xml:space="preserve"
				width="512px"
				height="512px"
			>
				<path
					d="M55.146,51.887L41.588,37.786c3.486-4.144,5.396-9.358,5.396-14.786c0-12.682-10.318-23-23-23s-23,10.318-23,23  s10.318,23,23,23c4.761,0,9.298-1.436,13.177-4.162l13.661,14.208c0.571,0.593,1.339,0.92,2.162,0.92  c0.779,0,1.518-0.297,2.079-0.837C56.255,54.982,56.293,53.08,55.146,51.887z M23.984,6c9.374,0,17,7.626,17,17s-7.626,17-17,17  s-17-7.626-17-17S14.61,6,23.984,6z"
				/>
			</svg>
		</span>
	</div>
</template>

<script>
export default {
	name: "Search",
	data() {
		return {
			searchResults: [],
			searchTerm: "",
			showResult: false,
		};
	},
	methods: {
		search: async function() {
			this.searchResults = await this.$call("search", {
				query: this.searchTerm,
			});
			if (this.searchResults.length == 0) {
				this.showResult = false;
			} else {
				this.showResult = true;
			}
		},
	},
};
</script>

<style lang="scss">
.search-results {
	width: 400px;
}

.search-input {
	width: 200px;
	transition: width 400ms;
	&:focus {
		width: 400px;
	}
}

.match {
	color: #38b2ac;
}
</style>
